}
.mce-tooltip {
  opacity: 1 !important;
  filter: alpha(opacity=100) !important;
}
.mce-tooltip-inner {
  font-size: 0.75rem !important;
  background-color: $black_2 !important;
  padding: 0.25rem 0.5rem !important;
  border-radius: 0.5rem;
