  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
    "Segoe UI Symbol", "Noto Color Emoji" !important;
}
.mce-tooltip-arrow {
  border-bottom-color: $black_2 !important;
}
.mce-menu-item:hover,
.mce-menu-item:focus {
  background: $grey_light_5 !important;
}

/*File Upload*/
.btn-file {
  overflow: hidden;
  position: relative;
  vertical-align: middle;
  display: inline-block;
  input {
    position: absolute;
    top: 0;
    right: 0;
    margin: 0;
    opacity: 0;
    filter: alpha(opacity=0);
    font-size: 1.5rem;
    height: 100%;
    width: 100%;
    direction: ltr;
    cursor: pointer;
  }
}
.fileinput-exists .fileinput-new,
.fileinput-new .fileinput-exists {
  display: none;
}
.fileinput {
  &.input-group
    > .input-group-append:last-child
    > .btn.btn-file:not(:last-child):not(.dropdown-toggle),
  &.input-group > .input-group-append:not(:last-child) > .btn.btn-file,
  &.input-group > .input-group-prepend > .btn.btn-file {
    border-top-right-radius: 0.5rem;
    border-bottom-right-radius: 0.5rem;
  }
  &.fileinput-exists {
    &.input-group
      > .input-group-append:last-child
      > .btn.btn-file:not(:last-child):not(.dropdown-toggle),
    &.input-group > .input-group-append:not(:last-child) > .btn.btn-file,
    &.input-group > .input-group-prepend > .btn.btn-file {
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
    }
  }
}

/*custom avatar uploader (HkAvatarUploader)*/
.avtuploder-circle {
  .avtuploder-wrapper {
    height: 7.5rem;
    width: 7.5rem;
    overflow: visible;
    border: 1px dashed $grey_light_3;
    border-radius: 0.25rem;
    font-family: inherit;
    color: $black_2;
    font-size: 1rem;
    cursor: pointer;

    .avtuploder-preview {
      border-radius: 50%;
      padding: 0;
    }
  }
}

.avtuploder-circle {
  .avtuploder-wrapper {
    border-radius: 50%;
  }
}

/*Dropzone*/
.dropzone {
  border: 1px dashed $grey_light_3;
  min-height: 100%;
  color: $black_2;
  border-radius: 0.25rem;
  .dz-message {
    margin: 3em 0;
    font-weight: 500;
    color: $black_2;
  }
  .dz-preview {
    .dz-details {
      color: $black_2;
    }
    .dz-filename span {
      background-color: transparent;
    }
    .dz-image {
      border-radius: 0.25rem;
    }
    &.dz-file-preview .dz-image {
      border-radius: 0.25rem;
    }
  }
}

.dropify-wrapper {
  border: 1px dashed $grey_light_3;
  border-radius: 0.25rem;
  font-family: inherit;
  color: $black_2;
  font-size: 1rem;
  padding: 1rem;
  .dropify-message {
    p {
      .main-text {
        display: block;
        color: $black_2;
        font-weight: 500;
      }
      .sub-text {
        font-size: 0.875rem;
        display: block;
        color: $grey_dark_3;
      }
    }
    span.file-icon {
      font-size: 2rem;
      color: $white;
      height: 3rem;
      width: 3rem;
      display: block;
      margin: auto;
      color: $grey;
      position: relative;
      &:before {
        content: "\F603";
        display: inline-block;
        font-family: bootstrap-icons !important;
        font-style: normal;
        font-weight: normal !important;
        font-variant: normal;
        text-transform: none;
        line-height: 1;
        vertical-align: -0.125em;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        top: 50%;
        position: absolute;
        left: 0;
        right: 0;
        margin: auto;
        @include transform(translateY(-50%));
      }
    }
  }
  .dropify-preview .dropify-infos {
    @include background-opacity($black, 0.85);
    .dropify-infos-inner p {
      font-weight: 500;
      &.dropify-infos-message {
        margin-top: 0px;
        padding-top: 0px;
        opacity: 1;
        font-weight: 700;
        text-transform: uppercase;
        letter-spacing: 0.5px;
        color: $grey;
        font-size: 0.875rem;
        &:before {
          display: none;
        }
      }
    }
  }
  .dropify-clear {
    border: none;
    font-size: 0.75rem;
    padding: 0.125rem 0.375rem;
    font-weight: 500;
    letter-spacing: 0.5px;
    top: 8px;
    right: 8px;
    &:hover {
      border-radius: 1rem;
    }
  }
  &:hover {
    background-image: none;
  }
}
.dropify-square,
.dropify-circle {
  .dropify-wrapper {
    height: 7.5rem;
    width: 7.5rem;
    overflow: visible;
    .dropify-message {
      font-size: 0.875rem;
      span.file-icon {
        font-size: 1.25rem;
        height: 1.25rem;
        width: 1.25rem;
      }
      p {
        font-size: 0.875rem;
      }
    }
    .dropify-preview {
      border-radius: 50%;
      padding: 0;
    }
    &:hover {
      .dropify-clear,
      .dropify-preview .dropify-infos {
        opacity: 0;
      }
    }
  }
}
.dropify-circle {
  .dropify-wrapper {
    border-radius: 50%;
  }
}
/*Form-Wizard*/
.wizard {
  > .steps {
    li > a {
      border: 1px solid $grey_light_3;
      border-radius: 0 !important;
      background: $white !important;
      font-weight: 500;
      position: relative;
      &:after {
        content: "\eb74";
        font-family: "remixicon";
        font-style: normal;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        position: absolute;
        display: flex;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        justify-content: center;
        -webkit-justify-content: center;
        -ms-flex-pack: center;
        height: 1rem;
        width: 1rem;
        background: $grey_light_3;
        border-radius: 50%;
        right: 0.5rem;
        top: 0.5rem;
        color: $white;
        font-size: 0.875rem;
        @include transition(all 0.1s ease);
      }
      &:before {
        content: "";
        position: absolute;
        background: transparent;
        height: 3px;
        width: 100%;
        left: 0;
        bottom: 0;
        @include transition(all 0.1s ease);
      }
    }
    .current {
      a,
      a:hover,
      a:active {
        color: $black_2;
        &:before {
          background: $primary;
        }
      }
      &.error {
        a,
        a:hover,
        a:active {
          color: $danger;
          &:before {
            background: $danger;
          }
        }
      }
    }
    .done {
      a,
      a:hover,
      a:active {
        color: $black_2;
        overflow: hidden;
        &:after {
          background: $success;
        }
        &:before {
          background: transparent;
        }
      }
    }
  }
  > .content {
    background: $white;
    border-radius: 0;
    min-height: 100px;
    margin: 0;
    border: none;
    padding-bottom: 0.625rem;
    > .body {
      width: 100%;
      height: auto;
      padding: 1.875rem;
      position: static;
      label.error {
        color: $danger;
      }
      input {
        @extend .form-control;
        &.error {
          background: $white;
          border-color: $danger !important;
          color: $black_2;
          &:focus {
            box-shadow: 0 0 0 0.125rem rgba($danger, 0.25);
          }
        }
      }
    }
  }
  > .actions {
    background: $white;
    padding: 0.625rem 1.875rem;
    a,
    a:hover,
    a:active {
      display: inline-block;
      font-weight: 400;
      text-align: center;
      white-space: nowrap;
      background: $primary;
      vertical-align: middle;
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;
      border: 1px solid $primary;
      text-transform: capitalize;
      outline: none;
      font-size: 1rem;
      padding: 0.375rem 1rem;
      border-radius: 0.5rem;
    }
    .disabled {
      a,
      a:hover,
      a:active {
        color: $white;
        background-color: $primary_light_3;
        border-color: $primary_light_3;
      }
    }
  }
}
@media (max-width: 991px) {
  .wizard .steps ul > li {
    width: 100%;
  }
}

/*Popover*/
.popover {
  border: none;
  border-radius: 0.5rem;
  font-family: var(--bs-body-font-family);
  box-shadow: 0 0px 10px 5px rgba(0, 0, 0, 0.1);
}
.popover-header {
  color: $black_2;
  background-color: $white;
  padding: 0.65rem 0.75rem;
  border-radius: 0.5rem 0.5rem 0 0;
  border-color: $grey_light_3;
}
.popover-body {
  padding: 0.65rem 0.75rem;
  color: $grey_dark_3;
  border-radius: 0.5rem;
  font-size: 1rem;
}

.bs-popover-auto[x-placement^="top"] > .arrow::before,
.bs-popover-top > .arrow::before {
  border-top-color: transparent;
}
.bs-popover-auto[x-placement^="bottom"] > .arrow::before,
.bs-popover-bottom > .arrow::before {
  border-bottom-color: transparent;
}
.bs-popover-auto[x-placement^="left"] > .arrow::before,
.bs-popover-left > .arrow::before {
  border-left-color: transparent;
}
.bs-popover-auto[x-placement^="right"] > .arrow::before,
.bs-popover-right > .arrow::before {
  border-right-color: transparent;
}

/*Collapse*/
.collapse-simple {
  > .card {
    margin-bottom: 0;
    box-shadow: none;
    border: 0;
    border-bottom: 1px solid $grey_light_4;
    border-radius: 0;
    padding: 0.5rem 0;
    &:last-child {
      border-bottom: 0;
    }
    > .card-header {
      padding: 0;
      border: 0;
      > * {
        font-size: 0.875rem;
        font-weight: 500;
        padding: 0.5rem 0;
        display: block;
        width: 100%;
        color: $primary;
        text-align: left;
        position: relative;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        background: transparent;
        border: none;
        justify-content: space-between;
        -webkit-justify-content: space-between;
        -ms-flex-pack: space-between;
        @include transition(all 0.2s ease-in-out);
        &::before {
          content: "\f2f9";
          font: normal normal normal 14px/1 "Material-Design-Iconic-Font";
          font-size: 1.25rem;
          text-rendering: auto;
          position: absolute;
          top: 8px;
          right: 0;
          @include color-opacity($black_2, 0.3);
        }
      }
      > * {
        &.collapsed {
          &:before {
            content: "\f2fc";
          }
        }
      }
    }
    > div {
      > .card-body {
        padding: 0.5rem 0;
      }
    }
  }
}

/*Accordion*/
.accordion {
  .accordion-button {
    color: $black_2;
    font-weight: 500;
    padding: 0.75rem 1.25rem;
    &:not(.collapsed) {
      background: $primary;
      color: $white;
      box-shadow: none;
      border-bottom: 1px solid $grey_light_3;
      &::after {
        background: none;
        @include color-opacity($white, 0.3);
      }
    }
    &:focus {
      box-shadow: none;
    }
    &::after {
      width: auto;
      height: auto;
      background: none;
      content: "\f2f9";
      font: normal normal normal 14px/1 "Material-Design-Iconic-Font";
      font-size: 1.25rem;
      text-rendering: auto;
      position: absolute;
      top: 12px;
      right: 20px;
      @include color-opacity($black_2, 0.3);
    }
  }
  .accordion-item {
    &:first-of-type {
      border-top-left-radius: calc(0.5rem - 1px);
      border-top-right-radius: calc(0.5rem - 1px);
      .accordion-button {
        border-top-left-radius: calc(0.5rem - 1px);
        border-top-right-radius: calc(0.5rem - 1px);
      }
    }
    &:last-of-type {
      border-bottom-left-radius: calc(0.5rem - 1px);
      border-bottom-right-radius: calc(0.5rem - 1px);
      .accordion-button.collapsed {
        border-bottom-right-radius: calc(0.5rem - 1px);
        border-bottom-left-radius: calc(0.5rem - 1px);
      }
    }
  }
  &.accordion-flush {
    .accordion-item {
      &:first-of-type {
        border-radius: 0;
        .accordion-button {
          border-radius: 0;
        }
      }
      &:last-of-type {
        border-radius: 0;
        .accordion-button.collapsed {
          border-radius: 0;
        }
      }
    }
  }
  &.accordion-card {
    > .accordion-item {
      margin-bottom: 1.875rem;
      border-bottom: 1px;
      border: 1px solid $grey_light_3;
      border-radius: calc(0.5rem - 1px);
      overflow: hidden;
    }
    &.accordion-card-shadow > .accordion-item {
      box-shadow: 0 2px 2px rgba(0, 0, 0, 0.1);
    }
    &.accordion-card-bold {
      > .accordion-item {
        .accordion-body {
          background: $primary;
          color: $white;
        }
      }
    }
  }
  &.accordion-soft {
    .accordion-button {
      &:not(.collapsed) {
        background: $primary_light_5;
        color: $primary;
        &:after {
          @include color-opacity($black_2, 0.3);
        }
      }
    }
    &.accordion-card {
      &.accordion-card-bold {
        > .accordion-item {
          .accordion-body {
            background: $primary_light_5;
            color: $primary;
          }
        }
      }
    }
    &.accordion-soft-content {
      .accordion-button {
        &:not(.collapsed) {
          background: transparent;
        }
      }
      > .accordion-item {
        .accordion-body {
          background: $primary_light_5;
          color: $black_2;
        }
      }
    }
  }
  &.accordion-simple {
    .accordion-button {
      &:not(.collapsed) {
        background: transparent;
        color: $primary;
        &::after {
          @include color-opacity($black_2, 0.3);
        }
      }
    }
    &.accordion-card {
      &.accordion-card-bold {
        .accordion-button {
          border-color: transparent;
        }
        .accordion-item .accordion-body {
          background: transparent;
          color: inherit;
        }
      }
    }
  }
  &.accordion-icon {
    .accordion-button {
      .acon-icon {
        min-width: 30px;
        display: inline-block;
        margin-right: 0.625rem;
        font-size: 1.5rem;
      }
    }
  }
}

/*Table*/
.table {
  --bs-table-striped-color: #{$black_2};
  --bs-table-active-color: #{$black_2};
  --bs-table-hover-color: #{$black_2};
  --bs-table-hover-bg: #{$grey_light_5};
  color: $grey_dark_3;
  border-color: $grey_light_4;
  thead th,
  tfoot th {
    vertical-align: middle;
    font-weight: 500;
    color: $black_2;
    font-size: 0.875rem;
    text-transform: capitalize;
    .form-check {
      font-size: 1rem;
    }
  }
  td,
  th {
    vertical-align: middle;
    padding: 0.75rem 1.25rem;
    a.table-link-text {
      &:hover {
        text-decoration: underline;
      }
    }
  }
  &.table-fixed {
    table-layout: fixed;
  }
  &.table-lg {
    td,
    th {
      padding: 1rem 1.5rem;
    }
  }
  &:not(.table-borderless) {
    > :not(:first-child) {
      border-top: 0;
    }
  }
  &.table-sm {
    td,
    th {
      padding: 0.625rem 1rem;
    }
  }
  &.nowrap {
    th,
    td {
      white-space: nowrap;
    }
  }
  .tb-truncate {
    max-width: 1px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    > * {
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
  .thead-primary th,
  .thead-success th,
  .thead-info th,
  .thead-warning th,
  .thead-danger th,
  .thead-secondary th,
  .thead-dark th {
    color: $white;
  }
  .thead-success th {
    background-color: $success;
  }
  .thead-info th {
    background-color: $info;
  }
  .thead-warning th {
    background-color: $warning;
  }
  .thead-danger th {
    background-color: $danger;
  }
  .thead-secondary th {
    background-color: $secondary;
  }
  .thead-primary th {
    background-color: $primary;
  }
  .thead-light th {
    background-color: $grey_light_5;
  }
  .thead-dark th {
    background-color: $black_2;
  }
  &.table-dark {
    thead th,
    tfoot th {
      color: $white;
    }
  }
  &.table-fc-fixed {
    thead th:first-child {
      position: sticky;
      left: 0;
      z-index: 2;
      background: $white;
    }
    tbody {
      th,
      tr td:first-child {
        position: sticky;
        left: 0;
        z-index: 2;
        background: $white;
      }
    }
  }
  &.table-lc-fixed {
    thead th:last-child {
      position: sticky;
      right: 0;
      z-index: 2;
      background: $white;
    }
    tbody {
      tr td:last-child {
        position: sticky;
        right: 0;
        z-index: 2;
        background: $white;
      }
    }
  }
  table thead th:first-child {
    position: sticky;
    left: 0;
    z-index: 2;
  }
}
.table-row-gap {
  td {
    border: 0 !important;
  }
}
table.sticky-table-header thead tr th {
  position: sticky;
  top: 64px;
  z-index: 99;
}
.table-action-hover {
  .table-action {
    opacity: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background: $white;
    right: 1.25rem;
    transition: all 0.1s;
    &::before {
      width: 120px;
      content: "";
      height: 100%;
      margin-left: -120px;
      position: absolute;
      top: 0;
      left: 0;
      background-image: linear-gradient(
        90deg,
        rgba(255, 255, 255, 0) 0%,
        $white 100%
      );
    }
  }
  tr:hover {
    .table-action {
      opacity: 1;
    }
  }
  &.table-hover {
    .table-action {
      background-color: var(--bs-table-hover-bg);
      &::before {
        background-image: linear-gradient(
          90deg,
          rgba(255, 255, 255, 0) 0%,
          var(--bs-table-hover-bg) 100%
        );
      }
    }
  }
}

@each $color, $value in $table-variants {
  @include table-variant($color, $value);
}

@media (max-width: 1200px) {
  .table-responsive .table > tbody > tr > td,
  .table-responsive .table > tbody > tr > th,
  .table-responsive .table > tfoot > tr > td,
  .table-responsive .table > tfoot > tr > th,
  .table-responsive .table > thead > tr > td,
  .table-responsive .table > thead > tr > th {
    white-space: nowrap;
  }
}
/*Data Table*/
table {
  &.hk-data-table {
    thead {
      td,
      th {
        position: relative;
        background-image: none !important;

        &.sorting_asc,
        &.sorting_desc {
          color: $primary;
          background-image: none !important;
        }

        &.sorting::before,
        &.sorting_asc::before,
        &.sorting_desc::before,
        &.sorting::after,
        &.sorting_asc::after,
        &.sorting_desc::after {
          display: inline-block;
          font-family: remixicon;
          font-style: normal;
          -webkit-font-smoothing: antialiased;
          -moz-osx-font-smoothing: grayscale;
          font-size: 0.875rem;
          text-transform: none;
          text-rendering: auto;
          line-height: 1;
          position: absolute;
          z-index: 10;
          right: 7px;
          top: 50%;
          @include transform(translateY(-50%));
        }

        &.sorting::before,
        &.sorting_asc::before,
        &.sorting_desc::before,
        &.sorting::after,
        &.sorting_asc::after,
        &.sorting_desc::after {
          content: "";
          bottom: auto;
          opacity: 1;
        }

        &.sorting::before {
          content: "\ea72";
        }

        &.sorting {
          &.sorting_asc::before {
            content: "\f0f9";
          }

          &.sorting_desc::after {
            content: "\f0fa";
          }

          &.sorting_desc::before {
            content: "";
          }
        }
      }
    }

    tbody {
      tr {
        &.selected {
          td {
            background-color: $primary_light_5;
            color: $black_2;
          }
        }

        &.group,
        &.group:hover {
          color: $primary !important;
          font-weight: 600;
          text-transform: uppercase;
        }

        // add for favorite star mark
        .fav-star {
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          margin-left: 0.625rem;
          font-size: 1rem;

          > .feather-icon > svg,
          .svg-icon > svg {
            cursor: pointer;

            &:hover {
              fill: $yellow;
              stroke: $yellow;
            }
          }

          &.marked {
            > .feather-icon > svg,
            .svg-icon > svg {
              fill: $yellow;
              stroke: $yellow;

              &:hover {
                fill: none;
                stroke: currentColor;
              }
            }
          }
        }
      }
    }
  }

  &.dt-rowReorder-float {
    outline-offset: 0;
    outline-color: $primary;
  }
}

div.dataTables_wrapper {
  .dataTables_length {
    select {
      margin-right: 0.625rem;
    }
  }

  div.dataTables_filter input {
    margin-left: 0 !important;
    width: 100% !important;
  }

  .dataTables_scrollBody {
    thead th {
      overflow: hidden;
    }

    border-left: 0 !important;

    table {
      margin-bottom: 1rem !important;
    }
  }

  div.dataTables_info {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    align-items: center;
    height: 100%;
    padding-top: 0;
  }
}

/*Advance Table*/
.table-advance {
  th,
  td {
    border: none;
  }
  tbody tr {
    @include transition(all 200ms cubic-bezier(0.4, 0, 0.2, 1));
    border-radius: 0.5rem;
    th,
    td {
      border: none;
      position: relative;
      &:after {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        border-top: 1px solid $grey_light_3;
        border-bottom: 1px solid $grey_light_3;
        left: 0;
        top: 0;
        pointer-events: none;
      }
      &:first-child {
        &:after {
          border-left: 1px solid $grey_light_3;
          border-radius: 0.5rem 0 0 0.5rem;
        }
      }
      &:last-child {
        &:after {
          border-right: 1px solid $grey_light_3;
          border-radius: 0 0.5rem 0.5rem 0;
        }
      }
    }
    &.table-row-gap {
      box-shadow: none;
      pointer-events: none;
      th,
      td {
        padding: 0.5rem;
        &:after {
          display: none;
        }
      }
    }
  }
  &.table-striped tbody tr:nth-of-type(odd) {
    background-color: transparent;
  }
}

/*Apex Chart*/
.apexcharts-legend {
  .apexcharts-legend-marker {
    margin-right: 0.625rem;
  }
}
.apexcharts-toolbar {
  max-width: inherit !important;
  .apexcharts-zoom-icon,
  .apexcharts-zoomin-icon,
  .apexcharts-zoomout-icon,
  .apexcharts-reset-icon,
  .apexcharts-pan-icon,
  .apexcharts-selection-icon,
  .apexcharts-menu-icon,
  .apexcharts-toolbar-custom-icon {
    height: auto;
    width: auto;
    color: $black_2;
    @include transform(scale(1));
    margin-left: 1rem;
    margin-right: 0;
  }
  svg {
    fill: none !important;
    height: 18px;
    width: 18px;
  }
  .apexcharts-menu {
    @extend .dropdown-menu;
    min-width: 10rem;
    padding: 0.5rem 0;
    margin: 0.125rem 0 0;
    .apexcharts-menu-item {
      padding: 0.25rem 1.5rem;
      text-align: left;
      color: $black_2;
      font-size: 1rem;
      &:hover {
        background-color: $grey_light_5;
      }
    }
  }
}
.apexcharts-canvas {
  .apexcharts-xaxistooltip,
  .apexcharts-yaxistooltip {
    font-size: 0.75rem;
    background: $black_2;
    color: $white;
    border-radius: 0.5rem;
    max-width: 200px;
    padding: 0.25rem 0.5rem;
    text-align: center;
    &:before,
    &:after {
      display: none;
    }
  }
  .apexcharts-tooltip.apexcharts-theme-dark {
    border-radius: 0.5rem;
    max-width: 200px;
    text-align: center;
    font-size: 0.75rem;
  }
  .apexcharts-tooltip.apexcharts-theme-light {
    border-radius: 0.5rem;
    font-size: 1rem;
    color: $grey_dark_3;
    border: none;
    box-shadow: 0 0px 10px 5px rgba(0, 0, 0, 0.1);
    .apexcharts-tooltip-title {
      color: $white;
      background-color: $primary;
      border-bottom: none;
      padding: 0.65rem 0.75rem;
      border-radius: 0.5rem 0.5rem 0 0;
    }
    .apexcharts-tooltip-series-group {
      padding-left: 0.75rem;
      padding-right: 0.75rem;
    }
  }
}
#chart_year,
#chart_quarter {
  width: 96%;
  max-width: 48%;
  box-shadow: none;
  padding-left: 0;
  padding-top: 1.25rem;
  background: $white;
}

#chart_year {
  float: left;
  position: relative;
  @include transition(1s ease transform);
  z-index: 3;
}

#chart_year.chart_quarter-activated {
  transform: translateX(0);
  @include transition(1s ease transform);
}

#chart_quarter {
  float: left;
  position: relative;
  z-index: -2;
  @include transition(1s ease transform);
}

#chart_quarter.active {
  @include transition(1.1s ease-in-out transform);
  @include transform(translateX(0));
  z-index: 1;
}

.apexcharts-datalabel-value {
  fill: $black_2 !important;
}
@media screen and (min-width: 480px) {
  #chart_year {
    @include transform(translateX(50%));
  }
  #chart_quarter {
    @include transform(translateX(-50%));
  }
}

/*charts*/
.hk-charts-pg {
  .hk-container-mwlg .card {
    .card-footer {
      @include transition(all 0.2s ease);
      min-height: 139px;
    }
    &:hover {
      .card-footer {
        background: $grey_light_5;
      }
    }
  }
}

/*Nestable*/
.dd-handle {
  @extend .form-control;
  font-weight: 500;
  margin: 0 0 0.375rem;
  padding: 0.5rem 1rem;
  background: $white;
  color: $black_2;
  height: 37px;
  border-width: 1px;
  border-color: $grey_light_3;
  &:hover,
  &:focus,
  &:active {
    color: $black_2;
    background: $grey_light_5;
  }
}
.dd-item > button {
  margin: 0;
  height: 37px;
  font-size: 1.5rem;
  width: 42px;
  color: $grey;
  outline: none;
  &.dd-expand:before,
  &.dd-collapse:before {
    font-family: "Line Awesome Free";
    font-weight: 900;
    line-height: 1;
  }
  &.dd-expand::before {
    content: "\f067";
  }
  &.dd-collapse::before {
    content: "\f068";
  }
  &:hover,
  &:focus,
  &:active {
    outline: none;
  }
}
.dd-placeholder {
  background: transparent;
  border-color: $grey_light_3;
}
.dd3-content {
  display: block;
  width: 100%;
  border-radius: 0.5rem;
  font-weight: 500;
  margin: 0 0 0.375rem;
  font-size: 0.875rem;
  padding: 0.5rem 1rem 0.5rem 4rem;
  background: $white;
  color: $black_2;
  border: 1px solid $grey_light_3;
  &:hover,
  &:focus,
  &:active {
    color: $black_2;
    background: $grey_light_5;
  }
}
.dd-dragel > .dd3-item > .dd3-content {
  margin: 0;
}
.dd3-item > button {
  margin-left: 2.625rem;
}
.dd3-handle {
  position: absolute;
  margin: 0;
  left: 0;
  top: 0;
  cursor: grab;
  color: $grey;
  width: 42px;
  text-indent: 100%;
  white-space: nowrap;
  overflow: hidden;
  padding: 1.125rem;
  border-color: $grey_light_3;
  border-radius: 0.5rem 0 0 0.5rem;
  &:before {
    font-family: "Line Awesome Free";
    font-weight: 900;
    color: $grey;
    content: "\f2a1";
    font-size: 1.625rem;
    color: $grey_light_3;
    display: block;
    position: absolute;
    left: 0;
    top: 8px;
    width: 100%;
    text-align: center;
    text-indent: 0;
  }
}
/*Treeview*/
.jstree-default {
  .jstree-clicked,
  .jstree-hovered {
    background: none;
    box-shadow: none;
    color: $blue;
  }
}
/*Modals*/
.modal-content,
.modal-header,
.modal-footer {
  border-color: $grey_light_3;
}
.modal-content {
  border-radius: 0.5rem;
  overflow: hidden;
  border: none;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.1);
}
.modal-header {
  border-top-left-radius: calc(0.5rem - 1px);
  border-top-right-radius: calc(0.5rem - 1px);
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  &.header-wth-bg {
    background: $grey_light_5;
    border-bottom: 0;
  }
  &.header-wth-bg-inv {
    background: $primary;
    border-bottom: 0;
    .modal-title {
      color: $white;
    }
  }
}
.modal-body {
  > .btn-close {
    position: absolute;
    right: 1.5rem;
    top: 1.25rem;
    ~ :is(h1, h2, h3, h4, h5, h6) {
      padding-right: 1.5rem;
      margin-top: 0.5rem;
    }
  }
}
.modal-footer {
  border-bottom-right-radius: calc(0.5rem - 1px);
  border-bottom-left-radius: calc(0.5rem - 1px);
}
.modal-backdrop {
  background-color: $black_2;
}
