/*Close*/
.btn-close {
  color: $black_2;
  opacity: 0.6;
  outline: none;
  height: auto;
  width: auto;
  text-shadow: none;
  font: normal normal normal 0/1 "Material-Design-Iconic-Font";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  &:before {
    content: "\f136";
    font-size: 1rem;
  }
  &:focus {
    box-shadow: none;
  }
  &:not(:disabled):not(.disabled):not(.btn-close-white):focus,
  &:not(:disabled):not(.disabled):not(.btn-close-white):hover {
    color: $grey_dark_3;
    opacity: 1;
  }
  span {
    display: none;
  }
}
.close-over {
  .btn-close {
    height: 22px;
    width: 22px;
    background: $grey_dark_3;
    color: $white !important;
    border-radius: 50%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    justify-content: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    cursor: pointer;
    position: absolute;
    right: -11px;
    top: -11px;
    opacity: 1;
    &:before {
      content: "\f136";
      font-size: 1rem;
      line-height: 0;
    }
    &:hover {
      background: $black_2;
    }
  }
}
/*Sweet-alert*/
.swal2-container {
  .swal2-content {
    color: $grey_dark_3;
  }
  .swal2-title {
    color: $black_2;
  }
  .swal2-popup {
    border-radius: 0.5rem;
    .swal2-validation-message {
      background: transparent;
      align-items: start;
      border: none;
      color: $red;
      &:before {
        content: "\ED77";
        font-family: remixicon !important;
        font-style: normal;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        background: transparent;
        color: $red;
        min-width: auto;
      }
    }
    .swal2-image {
      border-radius: 2rem;
