    }
  }
  &.alert-square {
    border-radius: 0;
  }
}
.alert-dismissible {
  padding-right: 3rem;
}
.jq-toast-wrap {
  width: 300px;
  .jq-toast-single {
    color: $black_2;
    border-radius: 0.5rem;
    margin: 0;
    border: none;
    background: $white;
    @extend .main-font;
    font-size: 0.875rem;
    line-height: 1.3;
    .jq-toast-loader {
      border-radius: 2px 2px 0 0;
      height: 2px;
    }
    a {
      color: inherit;
      text-decoration: underline;
      font-weight: inherit;
      border-bottom: none;
      padding-bottom: 0;
      font-size: inherit;
    }
    .close-jq-toast-single {
      font-size: 1.5rem;
      top: 0;
    }
    .jq-toast-heading {
      font-size: 1rem !important;
      color: inherit;
      font-weight: 500;
      @extend .main-font;
    }
    &.jq-toast-primary {
      background: $primary_light_5;
      color: $primary;
      border: 1px solid;
      @include border-opacity($primary_light_4, 0.3);
    }
    &.jq-toast-secondary {
      background: $secondary_light_5;
      color: $secondary;
      border: 1px solid;
      @include border-opacity($secondary_light_4, 0.3);
    }
    &.jq-toast-info {
      background: $info_light_5;
      color: $info;
      border: 1px solid;
      @include border-opacity($info_light_4, 0.3);
    }
    &.jq-toast-success {
      background: $success_light_5;
      color: $success;
      border: 1px solid;
      @include border-opacity($success_light_4, 0.3);
    }
    &.jq-toast-warning {
      background: $warning_light_5;
      color: $warning;
      border: 1px solid;
      @include border-opacity($warning_light_4, 0.3);
    }
    &.jq-toast-danger {
      background: $danger_light_5;
      color: $danger;
      border: 1px solid;
      @include border-opacity($danger_light_4, 0.3);
    }
    &.jq-toast-light {
      background: $grey_light_5;
      color: $grey_dark_4;
      border: 1px solid;
      @include border-opacity($grey_light_3, 1);
    }
    &.jq-toast-dark {
      background: $black_2;
      color: $white;
      border: 1px solid;
      @include border-opacity($black_1, 1);
    }
    &.jq-has-icon {
      padding: 0.75rem 1.5rem 0.875rem 2.75rem;
      .jq-toast-icon {
        position: absolute;
        font-size: 1rem;
        left: 11px;
        top: 14px;
      }
    }
    &.jq-toast-inv {
      color: $white;
      border: none;
      background: $black_2;
      &.jq-toast-inv-primary {
        background: $primary;
        color: $primary_light_5;
      }
      &.jq-toast-inv-danger {
        background: $danger;
        color: $danger_light_5;
      }
      &.jq-toast-inv-warning {
        background: $warning;
        color: $warning_light_5;
      }
      &.jq-toast-inv-info {
        background: $info;
        color: $info_light_5;
      }
      &.jq-toast-inv-success {
        background: $success;
        color: $success_light_5;
      }
    }
  }
}
.bottom-center-fullwidth {
  .jq-toast-wrap {
    left: 0 !important;
    right: 0 !important;
    width: 100% !important;
    bottom: 0 !important;
    .jq-toast-single {
      border-radius: 0;
    }
  }
}
.top-center-fullwidth {
  .jq-toast-wrap {
    left: 0 !important;
    right: 0 !important;
    width: 100% !important;
    top: 2px !important;
    .jq-toast-single {
      border-radius: 0;
    }
  }
}
@media (max-width: 576px) {
  .jq-toast-wrap {
    &.top-right,
    &.top-left,
    &.bottom-right,
    &.bottom-left {
      left: 0;
      right: 0;
      width: 100%;
    }
  }
}
/*Toast*/
.toast {
  border: none;
  border-radius: 0.5rem;
}
/*Bootstrap Notify*/
span[data-notify="icon"] {
  position: relative;
  top: 3px;
  line-height: 0;
  font-size: 1rem;
}
/*Tooltop*/
.tooltip {
  font-size: 0.75rem;
  font-family: var(--bs-body-font-family);
  inset: auto auto 0px 0px;
  .tooltip-inner {
    background: $black_2;
    border-radius: 0.5rem;
  }
  &.show {
    opacity: 1;
  }
  .tooltip-arrow {
    display: none;
  }
}
@media (max-width: 1024px) {
  .tooltip {
    display: none !important;
  }
}
/*Images*/
.img-thumbnail {
  border-color: $grey_light_3;
  border-radius: 0;
}
.img-background {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
/*Icons*/
.font-icons-wrap {
  li {
    text-align: center;
    vertical-align: middle;
    padding: 1.875rem 0.625rem 1.875rem 0.625rem;
    height: 100px;
    width: 100px;
    margin: 0.625rem;
    min-height: 60px;
    border: solid 1px #eee;
    border-radius: 0.5rem;
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    justify-content: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    cursor: pointer;
    a {
      color: $black_2;
      position: relative;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      justify-content: center;
      -webkit-justify-content: center;
      -ms-flex-pack: center;
      font-size: 1.25rem;
      > i {
        font-size: 1.625rem;
        line-height: 20px;
      }
      span {
        display: inline-block;
        position: absolute;
        background-color: $blue;
        color: $white;
        padding: 0.25rem 0.5rem;
        font-size: 0.75rem;
        white-space: nowrap;
        top: 45px;
        left: 50%;
        min-width: 98px;
        display: none;
        border-radius: 0.25rem;
        @include transform(translateX(-50%));
      }
    }
    &:hover {
      background: $black_2;
      a {
        color: $white;
        span {
          display: block;
        }
      }
    }
  }
}
.feather {
  stroke: currentColor;
  stroke-width: 2;
  stroke-linecap: round;
  stroke-linejoin: round;
  fill: none;
}
.svg-icon {
  line-height: 0 !important;
}
.feather-icon {
  line-height: 0 !important;
  &.fe-x {
    > svg {
      height: 16px;
      width: 16px;
    }
  }
  &.fe-2x {
    > svg {
      height: 32px;
      width: 32px;
    }
  }
}
.feather-icon > svg,
.svg-icon > svg {
  height: 1em;
  width: 1em;
}
.flag-icon {
  height: 20px;
  width: 20px;
}

/*Carousel*/
.carousel-control-prev,
.carousel-control-next {
  opacity: 1;
  &:hover {
    .carousel-control-next-icon,
    .carousel-control-prev-icon {
      @include border-opacity($white, 1);
    }
  }
}
.carousel-control-next-icon,
.carousel-control-prev-icon {
  height: 50px;
  width: 50px;
  border: 2px solid;
  @include border-opacity($white, 0.5);
  border-radius: 50%;
  background: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  justify-content: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  font-size: 1.25rem;
  &:before {
    display: inline-block;
    font: normal normal normal 22px/1 "Material-Design-Iconic-Font";
    text-rendering: auto;
    padding-left: 0.25rem;
    // speak: none;
    text-transform: none;
    text-rendering: auto;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
}
.carousel-control-next-icon:before {
  content: "\f2fb";
}
.carousel-control-prev-icon:before {
  content: "\f2fa";
}
.custom-carousel-nav {
  .carousel-text-nav {
    position: static;
    color: $black_2;
    &:hover,
    &:focus {
      color: $black_2;
    }
  }
  .carousel-indicators {
    position: static;
  }
}

.carousel-indicators {
  bottom: 20px;
  li[data-bs-target] {
    width: 6px;
    height: 6px;
    margin-right: 0.25rem;
    margin-left: 0.25rem;
    border: 1px solid $white;
    border-radius: 50%;
    background: transparent;
    &.active {
      @include background-opacity($grey_light_1, 1);
    }
  }
  &.dark-indicators {
    li {
      border-color: $black_2;
      &.active {
        @include background-opacity($black, 1);
      }
    }
  }
  &.theme-indicators {
    li {
      border-color: $primary;
      &.active {
        @include background-opacity($primary, 1);
      }
    }
  }
}
.carousel-caption {
  bottom: 30px;
}
.swiper-carousel {
  .swiper-item {
    height: auto;
  }

  .swiper-content {
    background-size: cover;
  }
  .swiper-slide {
    margin-bottom: 20px;
  }
  .swiper-button-next,
  .swiper-button-prev {
    color: $white;
  }
  .swiper-pagination-bullet {
    opacity: 0.5;
    background: $black_2;
  }
  .swiper-pagination-bullet-active {
    opacity: 1;
    background: $black_2;
  }

  .swiper-horizontal > .swiper-pagination-bullets,
  .swiper-pagination-bullets.swiper-pagination-horizontal,
  .swiper-pagination-custom,
  .swiper-pagination-fraction {
    bottom: -5px;
  }
}
.owl-carousel {
  .item-video {
    height: auto;
  }
  .owl-video-tn {
    background-size: cover;
  }
  .owl-dots .owl-dot {
    span {
      width: 8px;
      height: 8px;
      margin: 0 0.25rem;
      background: transparent;
      border: 1px solid $black_2;
    }
    &:focus {
      outline: none;
    }
    &:hover {
      span {
        background: $black_2;
      }
    }
    &.active {
      span {
        background: $black_2;
      }
    }
  }
  &.light-owl-dots {
    .owl-dots .owl-dot {
      span {
        border: 1px solid $white;
      }
      &:focus {
        outline: none;
      }
      &:hover {
        span {
          background: $white;
        }
      }
      &.active {
        span {
          background: $white;
        }
      }
    }
  }
  &.theme-owl-dots {
    .owl-dots .owl-dot {
      span {
        border: 1px solid $primary;
      }
      &:focus {
        outline: none;
      }
      &:hover {
        span {
          background: $primary;
        }
      }
      &.active {
        span {
          background: $primary;
        }
      }
    }
  }
  .owl-video-play-icon {
    @include background-opacity($white, 0.8);
    left: 20px;
    bottom: 20px;
    margin: 0;
    height: 35px;
    width: 35px;
    border-radius: 50%;
    top: auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    justify-content: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    color: $blue;
    &:before {
      display: inline-block;
      font: normal normal normal 22px/1 "Material-Design-Iconic-Font";
      text-rendering: auto;
      padding-left: 0.25rem;
      // speak: none;
      text-transform: none;
      text-rendering: auto;
      line-height: 1;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
      content: "\f3aa";
    }
    &:hover {
      @include transform(scale(1.1, 1.1));
    }
  }
  &.dots-on-item {
    .owl-dots {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 15px;
    }
  }
}
@media (max-width: 991px) {
  .carousel-control-next-icon,
  .carousel-control-prev-icon {
