}
.activity-list-h {
  flex-direction: row;
  .list-group-item {
    padding: 0;
    border: none;
    display: flex;
    flex-direction: column;
    padding-bottom: 6.25rem;
    align-items: center;
    .media {
      flex-flow: column;
      align-items: center;
      flex: 1;
      .media-body {
        width: 400px;
        padding: 0 0.625rem;
