/*Progressbar*/
.progress {
  border-radius: 0;
  &.progress-bar-rounded {
    border-radius: 50rem;
    .progress-bar {
      border-radius: 50rem;
    }
  }
  &.progress-bar-xs {
    height: 5px;
  }
  &.progress-bar-sm {
    height: 9px;
  }
  &.progress-bar-md {
    height: 13px;
  }
  &.progress-bar-lg {
    height: 18px;
  }
  &.progress-bar-xl {
    height: 20px;
  }
  .progress-bar {
    background-color: $primary;
    .progress-tooltip {
      position: absolute;
      right: 0-20px;
      width: 50px;
      background: $black_2;
      bottom: 25px;
      border-radius: 0.3rem;
      padding: 0.25rem 0.625rem;
      font-size: 0.75rem;
      height: 30px;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      justify-content: center;
      -webkit-justify-content: center;
      &:after {
        content: "";
        position: absolute;
        width: 0;
        height: 0;
        bottom: -10px;
        left: -5px;
        right: 0;
        margin: auto;
        box-sizing: border-box;
        border: 0.6em solid rgba(240, 210, 210, 0.1);
        border-color: transparent transparent $black_2 $black_2;
        transform-origin: 0 0;
        @include transform(rotate(-45deg));
      }
    }
  }
  &.progress-width-animated {
    overflow: visible !important;
    .progress-bar {
      overflow: visible !important;
      position: relative;
    }
  }
}
.progress-label {
  text-transform: capitalize;
  font-size: 0.875rem;
  font-weight: 400;
  margin-bottom: 0.5rem;
}
.progress-wrap {
  &.lb-side-left {
    .progress-lb-wrap {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      .progress-label {
        display: inline-block;
        padding-right: 1rem;
        flex: 0 0 auto;
        -ms-flex: 0 0 auto;
        margin-bottom: 0;
      }
      .progress {
        flex: 1 1 100%;
