/*Hopscotch*/
div.hopscotch-bubble {
  border: none;
  box-shadow: 0px 4px 16px 0 rgba(0, 0, 0, 0.12);
  border-radius: 0.25rem;
  font-size: 0.875rem;
  font-family: var(--bs-body-font-family);
  .hopscotch-bubble-container {
    .hopscotch-bubble-number {
      display: none;
    }
    .hopscotch-bubble-content {
      margin: 0;
    }
    h3 {
      color: $black_2;
      font-family: var(--bs-body-font-family);
      font-size: 1.1rem;
      font-weight: 500;
      line-height: 1.2;
      margin: -1px 16px 4px 0;
      padding: 0;
    }
    .hopscotch-content {
      color: $grey_dark_3;
      font-family: var(--bs-body-font-family);
      line-height: inherit;
      margin: 0.5rem 0;
      font-weight: inherit;
      padding: 0;
    }
    .hopscotch-actions {
      margin: 1.25rem 0 0;
      .hopscotch-nav-button {
        outline: none;
        background-image: none;
        font-weight: 400;
        margin: 0;
        overflow: visible;
        text-decoration: none !important;
        width: auto;
        height: auto;
        line-height: inherit;
        text-shadow: none;
        &:focus,
        &:active,
        &:hover {
          box-shadow: none !important;
        }
        @extend .btn;
        @extend .btn-sm;
        &.next {
          @extend .btn-primary;
        }
        &.prev {
          @extend .btn-outline-secondary;
          margin-right: 0.625rem;
        }
      }
    }
    .hopscotch-bubble-close {
      text-shadow: none;
      font: normal normal normal 14px/1 "Material-Design-Iconic-Font";
      text-rendering: auto;
      outline: none;
      color: $black_2;
      background: transparent;
      // webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
      text-indent: inherit;
      width: auto;
      height: auto;
      font-size: 0;
      z-index: 1;
      opacity: 0.6;
      right: 3px;
      &:before {
        content: "\f136";
        font-size: 1.125rem;
      }
      &:hover {
        opacity: 1;
      }
    }
  }
  .hopscotch-bubble-arrow-container {
    &.up {
      top: 2px;
      left: 32px;
    }
    &.down {
      bottom: -32px;
      left: 4px;
      &:after {
        @include transform(rotate(-45deg));
      }
    }
    &.right {
      top: 30px;
      right: -32px;
      &:after {
        @include transform(rotate(225deg));
      }
    }
    &.left {
      top: 10px;
      left: 2px;
      &:after {
        @include transform(rotate(45deg));
      }
    }
    .hopscotch-bubble-arrow-border,
    .hopscotch-bubble-arrow {
      display: none;
    }
    &:after {
      content: "";
      position: absolute;
      width: 0;
      height: 0;
      margin-left: 0;
      top: 0;
      left: 0;
      box-sizing: border-box;
      border: 0.6em solid rgba(0, 0, 0, 0.1);
      border-color: transparent transparent $white $white;
      transform-origin: 0 0;
      @include transform(rotate(135deg));
      box-shadow: -2px 2px 16px rgba(0, 0, 0, 0.05);
    }
  }
  &.animated {
    -webkit-animation-fill-mode: both;
    -moz-animation-fill-mode: both;
    -ms-animation-fill-mode: both;
    -o-animation-fill-mode: both;
    animation-fill-mode: both;
    -webkit-animation-duration: 1s;
    -moz-animation-duration: 1s;
    -ms-animation-duration: 1s;
    -o-animation-duration: 1s;
    animation-duration: 1s;
  }
  &.tour-hopscotch-dark {
    background: $primary_dark_3;
    .hopscotch-bubble-container {
      h3 {
        color: $white;
      }
      .hopscotch-content {
        color: $white;
      }
      .hopscotch-actions {
        .hopscotch-nav-button {
          &.next {
            @extend .btn-white;
          }
          &.prev {
            @extend .btn-outline-white;
            margin-right: 0.625rem;
          }
        }
      }
      .hopscotch-bubble-close {
        outline: none;
        color: $white;
      }
    }
    .hopscotch-bubble-arrow-container::after {
      border-color: transparent transparent $primary_dark_3 $primary_dark_3;
    }
  }
  @-webkit-keyframes fadeInUp {
    0% {
      opacity: 0;
      -webkit-transform: translateY(20px);
    }
    100% {
      opacity: 1;
      -webkit-transform: translateY(0);
    }
  }

  @-moz-keyframes fadeInUp {
