        margin-right: 0.5rem;
        display: flex;
      }
      .feather-icon > svg,
      .svg-icon > svg {
        height: 1em;
        width: 1em;
      }
    }
  }
  &.chip-ticked {
    > span {
      &:before {
        font: normal normal normal 0/1 "Material-Design-Iconic-Font";
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        content: "\f26b";
        @include transition(all 0.15s ease);
      }
      .chip-text {
        @include transition(all 0.15s ease);
      }
    }
    input {
      &:checked + span {
        &:before {
