    }
  }
  &.hk-ribbon-orange {
    background: $orange;
    &:before,
    &:after {
      border-color: $orange;
    }
  }
  &.hk-ribbon-pumpkin {
    background: $pumpkin;
    &:before,
    &:after {
      border-color: $pumpkin;
    }
  }
  &.hk-ribbon-brown {
    background: $brown;
    &:before,
    &:after {
      border-color: $brown;
    }
  }
  &.hk-ribbon-gold {
    background: $gold;
    &:before,
    &:after {
      border-color: $gold;
    }
  }
  &.hk-ribbon-smoke {
    background: $smoke;
    &:before,
    &:after {
      border-color: $smoke;
    }
  }
  &.hk-ribbon-grey {
    background: $grey;
    &:before,
    &:after {
      border-color: $grey;
    }
  }
  &.hk-ribbon-secondary {
    background: $secondary;
    &:before,
    &:after {
      border-color: $secondary;
    }
  }
  &.hk-ribbon-light {
    background: $grey_light_4;
