}

/*Advance Badges*/
.badge-icon {
  width: 4rem;
  height: 4.313rem;
  font-size: 2.25rem;
  position: relative;
  display: inline-block;
  vertical-align: middle;
  > svg {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    height: 100%;
    width: 100%;
  }
  .badge-icon-wrap {
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    justify-content: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    height: 100%;
    width: 100%;
    color: $white;
    position: relative;
    z-index: 1;
  }
  &.badge-icon-lg {
    width: 6rem;
    height: 6.031rem;
    font-size: 3.25rem;
  }
  &.badge-icon-md {
    width: 5rem;
    height: 5.031rem;
    font-size: 2.75rem;
  }
  &.badge-icon-sm {
    width: 3rem;
    height: 3.313rem;
    font-size: 1.75rem;
  }
  &.badge-icon-xs {
    width: 2rem;
    height: 2.313rem;
    font-size: 1.25rem;
  }
  &.badge-icon-xxs {
    width: 1.5rem;
    height: 1.813rem;
    font-size: 1rem;
  }
  &.badge-circle {
    height: 4rem;
    &.badge-icon-lg {
      height: 6rem;
    }
    &.badge-icon-md {
      height: 5rem;
    }
    &.badge-icon-sm {
      height: 3rem;
    }
    &.badge-icon-xs {
      height: 2rem;
    }
    &.badge-icon-xxs {
      height: 1.5rem;
    }
  }
  &.badge-level {
    position: relative;
    > span {
      position: absolute;
      bottom: 0;
      right: 16%;
      z-index: 1;
      font-weight: 600;
      color: inherit;
      font-size: 40%;
      background: transparent;
      &:before {
        content: attr(title);
        position: absolute;
        -webkit-text-stroke: 4px $white;
        left: 0;
        z-index: -1;
      }
    }
  }
}
.avatar {
  &.custom-badge-on-avatar {
    .badge-icon {
      font-size: inherit;
    }
  }
}

/*Callout*/
.callout {
  margin-bottom: 0;
  &.callout-floating {
    position: fixed;
    width: 300px;
    z-index: 1055;
    margin-bottom: 0;
    &.top-end {
      top: 25px;
      right: 25px;
    }
    &.bottom-end {
      bottom: 25px;
      right: 25px;
    }
    &.top-start {
      top: 25px;
      left: 25px;
    }
    &.bottom-start {
      bottom: 25px;
      left: 25px;
    }
    &.top-center {
      top: 25px;
      left: 50%;
      width: auto;
      @include transform(translateX(-50%));
    }
    &.bottom-center {
      bottom: 25px;
      left: 50%;
      width: auto;
      @include transform(translateX(-50%));
    }
  }
  &.callout-dark {
    color: $white;
    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    p {
      color: inherit;
    }
  }
}
/*Toolbar*/
.hk-toolbar {
  .nav {
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -ms-flex-pack: distribute;
    justify-content: space-around;
    height: 100%;
    .nav-item {
      height: 100%;
    }
  }
  &.toolbar-asymmetric {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
    justify-content: space-between;
  }
}

/*Filter Component*/
.btn-filter {
  color: $grey_dark_3;
  background-color: transparent;
  background-image: none;
  border-color: $grey_light_5;
  &:hover,
  &:focus {
    color: $grey_dark_3;
    background-color: transparent;
    background-image: none;
    border-color: $grey_light_5;
  }
  &.active {
    color: $primary;
    background-color: $primary_light_5;
    border-color: $primary;
    box-shadow: none !important;
    &:hover,
    &:focus {
      color: $primary;
      background-color: $primary_light_5;
      border-color: $primary;
    }
  }
}
.filter-block-wrap {
  .filter-block {
    &.card {
      .card-img {
        -o-object-fit: cover;
        object-fit: cover;
      }
      .card-img-overlay {
        background-color: rgba($black_2, var(--bs-bg-opacity));
      }
      input {
        position: absolute;
        z-index: 1;
        left: 0;
        top: 0;
        margin: 0;
        height: 100%;
        width: 100%;
        opacity: 0;
        cursor: pointer;
        &:checked ~ .card-img-overlay {
          background-color: rgba($blue, var(--bs-bg-opacity));
        }
      }
    }
  }
}
/*Content sidebar*/
[data-content-sidebar="active"] {
  &.hk-wrapper {
    .hk-pg-wrapper {
      margin-right: 17.5rem;
    }
    .hk-content-sidebar {
      position: fixed;
      top: 64px;
      overflow-y: auto;
      width: 280px;
      right: 0;
      z-index: 2;
      background: $white;
      padding-top: 1.5rem;
      border-left: 1px solid $grey_light_3;
      bottom: 0;
      .sidebar-content-wrap {
        padding: 1.25rem;
      }
    }
  }
}

/*Fixed Footer*/
.hk-fixed-footer {
  position: fixed;
  bottom: 0;
  z-index: 1032;
  background: $white;
  box-shadow: 1px 1px 3px 5px rgba(0, 0, 0, 0.1);
  right: 0;
  left: 0;
}
.hk-wrapper {
  &.fixed-footer-active {
    .hk-pg-wrapper {
      //   padding-bottom: 8.125rem;
    }
  }
}
/*Dragula*/
.gu-mirror {
  cursor: -webkit-grabbing;
  cursor: -moz-grabbing;
  cursor: grabbing;
  position: fixed !important;
  margin: 0 !important;
  z-index: 9999 !important;
  opacity: 1;
}
.dragula-demo {
  .dragula-block {
    border: 1px dotted $grey_light_1;
    border-radius: 0.25rem;
    padding: 0.625rem;
  }
  .dragula-block > div {
    border: 1px solid $grey_light_3;
    border-radius: 0.25rem;
    margin: 0.625rem;
    padding: 0.625rem;
    cursor: move;
  }
}

/*Authantication Pages*/
.hk-wrapper {
  &.hk-pg-auth {
    .hk-pg-wrapper {
      .hk-pg-body {
        .auth-content {
          min-height: calc(100vh - 249px);
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          -webkit-box-align: center;
          -ms-flex-align: center;
          align-items: center;
          justify-content: center;
          -webkit-justify-content: center;
          -ms-flex-pack: center;
        }
      }
      .v-separator {
        margin: 0;
        position: relative;
        &:after {
          height: 100%;
          right: 0;
        }
        &.separator-sm {
          &:after {
            height: 70%;
          }
        }
      }
      .auth-split {
        min-height: 100vh;
        .auth-content {
          min-height: 100vh;
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          -webkit-box-align: center;
          -ms-flex-align: center;
          align-items: center;
          justify-content: center;
          -webkit-justify-content: center;
          -ms-flex-pack: center;
          position: relative;
          mark {
            z-index: 1;
            color: $black_2;
            &:after {
              z-index: -1;
              background: $green;
              opacity: 1;
            }
          }
          .extr-link {
            font-size: 0.75rem;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-align: center;
            -ms-flex-align: center;
            align-items: center;
            justify-content: flex-end;
            -webkit-justify-content: flex-end;
            .feather-icon,
            .svg-icon {
              margin-right: 0.25rem;
              svg {
                height: 14px;
                width: 14px;
              }
            }
          }
        }
        .credit-text {
          position: absolute;
          left: 30px;
          bottom: 30px;
          mix-blend-mode: inherit;
        }
        .bg-img {
          position: absolute;
          left: 0;
          top: 0;
          max-width: 100%;
          object-fit: cover;
          height: 100%;
          z-index: -1;
        }
      }
    }
  }
}
@media (max-width: 767px) {
  .hk-wrapper {
    &.hk-pg-auth {
      .hk-pg-wrapper {
        .hk-pg-body {
          .container-xxl {
            .auth-content {
              min-height: auto;
              -webkit-box-align: flex-start;
              -ms-flex-align: flex-start;
              align-items: flex-start;
            }
          }
        }
      }
      .v-separator:after {
        display: none;
      }
    }
  }
}
