/*ChatApp*/
.chatapp-wrap {
	position: relative;
	overflow: hidden;
	height: calc(100vh - 65px);
	@include transition(height 0.2s ease);
	.chatapp-content {
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		height: 100%;
		position: relative;
		overflow: hidden;
		left: 0;
		@include transition(all 0.2s ease);
		.chatapp-aside {
			border-right: 1px solid $grey_light_4;
			background: $white;
			position: relative;
			display: -webkit-box;
			display: -ms-flexbox;
			display: flex;
			flex-flow: column;
			height: 100%;
			z-index: 1;
			left: 0;
			margin-top: 0;
			width: 370px;
			flex-shrink: 0;
			header.aside-header {
				display: -webkit-box;
				display: -ms-flexbox;
				display: flex;
				-webkit-box-align: center;
				-ms-flex-align: center;
				align-items: center;
				justify-content: space-between;
				-webkit-justify-content: space-between;
				-ms-flex-pack: space-between;
				height: 65px;
				padding: 0 1.25rem;
				border-bottom: 1px solid $grey_light_4;
				background: $white;
				color: $black_2;
				.chatapp-title {
					display: -webkit-box;
					display: -ms-flexbox;
					display: flex;
					-webkit-box-align: center;
					-ms-flex-align: center;
					align-items: center;
					h1 {
						font-size: 1.5rem;
						font-weight: 500;
						margin-bottom: 0;
					}
					&:after {
						top: 0;
					}
				}
				.btn {
					margin-right: 0.25rem;
					&:last-child {
						margin-right: 0;
					}
				}
			}
			.aside-body {
				-ms-flex: 1;
				flex: 1;
				height: 100%;
				overflow: auto;
				padding: 1.25rem;
				.aside-search {
					margin-bottom: 1.25rem;
				}
				.frequent-contact {
					margin-bottom: 1.25rem;
					ul {
						li {
							.avatar {
								cursor: pointer;
							}
						}
					}
				}
				.chat-contacts-list {
					margin: 0 -0.8rem;
					.list-group-item {
						padding: 0;
						border: none;
						margin-bottom: 0.25rem;
						&:last-child {
							margin-bottom: 0;
						}
						.media {
							padding: 0.75rem 0.8rem;
							border-radius: 0.5rem;
							@include transition(0.2s ease);
							-webkit-box-align: center;
							-ms-flex-align: center;
							align-items: center;
							display: -webkit-box;
							display: -ms-flexbox;
							display: flex;
							cursor: pointer;
							.media-head {
								position: relative;
								margin-right: 1rem;
								display: -webkit-box;
								display: -ms-flexbox;
								display: flex;
							}
							.media-body {
								display: -webkit-box;
								display: -ms-flexbox;
								display: flex;
								min-width: 0;
								> div:first-child {
									min-width: 0;
									flex: 1;
									.user-name,
									.user-last-chat,
									.user-status {
										overflow: hidden;
										text-overflow: ellipsis;
										white-space: nowrap;
									}
									.user-name {
										color: $black_2;
										font-weight: 500;
										margin-bottom: 0.25rem;
									}
								}
								> div:last-child {
									text-align: right;
									padding-left: 1rem;
									display: -webkit-box;
									display: -ms-flexbox;
									display: flex;
									flex-flow: column;
									justify-content: space-between;
									-webkit-justify-content: space-between;
									-ms-flex-pack: space-between;
									-webkit-box-align: flex-end;
									-ms-flex-align: flex-end;
									align-items: flex-end;
									width: 4.5rem;
									.last-chat-time {
										font-size: 0.75rem;
										color: $primary;
									}
									.badge {
										margin-top: 0.25rem;
									}
									.action-drp {
										opacity: 0;
										visibility: hidden;
										position: absolute;
										top: 15px;
										@include transition(all 0.2s ease-in-out);
									}
								}
							}
							&:hover,
							&:focus {
								background-color: #f2f2f2;
								.media-body {
									> div:last-child {
										.action-drp {
											opacity: 1;
											visibility: visible;
										}
										.last-chat-time,
										.badge-pill {
											opacity: 0;
											visibility: hidden;
											@include transition(all 0.2s ease-in-out);
										}
									}
								}
							}
							&.read-chat {
								.media-body {
									> div:last-child {
										.last-chat-time {
											color: $grey_dark_3;
										}
									}
								}
								&.active-user {
									background-color: #f2f2f2;
									.media-body {
										> div:first-child {
											.user-name {
												color: $primary;
											}
										}
									}
								}
							}
						}
					}
					.title-wth-divider {
						padding: 0 0.8rem;
						&:after {
							background: $grey_light_5;
						}
					}
				}
				.channels-list {
					.nav-item .nav-link {
						padding-left: 0;
						padding-right: 0;
					}
				}
			}
		}
		.chatapp-single-chat {
			display: -webkit-box;
			display: -ms-flexbox;
			display: flex;
			flex-grow: 1;
			flex-flow: column;
			height: 100%;
			background: $white;
			z-index: 10;
			header.chat-header {
				display: -webkit-box;
				display: -ms-flexbox;
				display: flex;
				-webkit-box-align: center;
				-ms-flex-align: center;
				align-items: center;
				justify-content: space-between;
				-webkit-justify-content: space-between;
				-ms-flex-pack: space-between;
				height: 65px;
				padding: 0 1.25rem;
				background: $white;
				border-bottom: 1px solid $grey_light_4;
				.back-user-list {
					display: none;
					margin-right: 0.625rem;
				}
				.media {
					-webkit-box-align: center;
					-ms-flex-align: center;
					align-items: center;
					.media-head {
						position: relative;
						display: -webkit-box;
						display: -ms-flexbox;
						display: flex;
						-webkit-box-align: center;
						-ms-flex-align: center;
						align-items: center;
						margin-right: 1rem;
					}
					.media-body {
						.user-name {
							font-size: 1rem;
							font-weight: 500;
							color: $black_2;
							text-transform: capitalize;
						}
						.user-status {
							font-size: 0.875rem;
						}
						.user-name,
						.user-status {
							overflow: hidden;
							text-overflow: ellipsis;
							white-space: nowrap;
							max-width: 150px;
						}
					}
				}
				.chat-options-wrap {
					display: -webkit-box;
					display: -ms-flexbox;
					display: flex;
					> a {
						margin-left: 0.25rem;
					}
				}
			}
			.chat-body {
				@include transition(all 0.2s ease);
				-ms-flex: 1;
				flex: 1;
				height: 100%;
				overflow: auto;
				ul.list-unstyled {
					width: 90%;
					margin: 0 auto;
					padding: 2rem 1.25rem;
				}
				.media {
					.media-body {
						margin-left: 0.75rem;
						.msg-box {
							display: -webkit-box;
							display: -ms-flexbox;
							display: flex;
							-webkit-box-align: center;
							-ms-flex-align: center;
							align-items: center;
							> div {
								padding: 0.625rem 1rem;
								border-radius: 1.25rem;
								display: inline-block;
								position: relative;
								p:first-child {
									color: $black_2;
								}
								.chat-time {
									display: block;
									text-transform: uppercase;
									font-size: 0.75rem;
									margin-top: 0.25rem;
									opacity: 0.5;
								}
							}
							+ .msg-box {
								margin-top: 0.25rem;
							}
							.msg-action {
								margin-left: 1rem;
								opacity: 0;
								display: -webkit-box;
								display: -ms-flexbox;
								display: flex;
								padding: 0;
								background: transparent !important;
								&:after {
									display: none;
								}
							}
							&:hover {
								.msg-action {
									opacity: 1;
								}
							}
						}
						&.msg-docs {
							display: -webkit-box;
							display: -ms-flexbox;
							display: flex;
							-ms-flex-wrap: wrap;
							flex-wrap: wrap;
							.msg-box {
								> div {
									cursor: pointer;
									.media {
										-webkit-box-align: center;
										-ms-flex-align: center;
										align-items: center;
										.file-name {
											color: $black_2;
											font-weight: 500;
											overflow: hidden;
											text-overflow: ellipsis;
											white-space: nowrap;
											max-width: 165px;
										}
										.file-size {
											font-size: 0.875rem;
											margin-top: 0.25rem;
										}
									}
									.file-overlay {
										position: absolute;
										opacity: 0;
										left: 0;
										top: 0;
										height: 100%;
										width: 100%;
										color: $white;
										@include background-opacity($black_2, 0.4);
										border-radius: 1.25rem;
										display: -webkit-box;
										display: -ms-flexbox;
										display: flex;
										-webkit-box-align: center;
										-ms-flex-align: center;
										align-items: center;
										justify-content: center;
										-webkit-justify-content: center;
										-ms-flex-pack: center;
									}
									&:hover {
										.file-overlay {
											opacity: 1;
										}
									}
									&.msg-action {
										&:after {
											display: none;
										}
									}
								}
								+ .msg-box {
									margin-top: 0;
								}
							}
						}
						&.msg-imgs {
							.msg-box {
								> div {
									padding: 0;
									img {
										border-radius: 1.25rem;
										max-width: 336px;
									}
								}
								&:first-child,
								&:last-child {
									> div {
										border-radius: 1.25rem !important;
									}
								}
							}
						}
					}
					&.received {
						margin-bottom: 1.25rem;
						.media-body {
							.msg-box {
								> div {
									background: #f2f2f2;
								}
								&:first-child {
									> div {
										border-top-left-radius: 0;
									}
								}
							}
						}
					}
					&.sent {
						margin-bottom: 1.25rem;
						.media-body {
							margin-left: 0;
							display: -webkit-box;
							display: -ms-flexbox;
							display: flex;
							justify-content: flex-end;
							-webkit-justify-content: flex-end;
							-ms-flex-pack: flex-end;
							-ms-flex-direction: column;
							flex-direction: column;
							-webkit-box-align: flex-end;
							-ms-flex-align: flex-end;
							align-items: flex-end;
							.msg-box {
								-ms-flex-direction: row-reverse;
								flex-direction: row-reverse;
								> div {
									background: $primary;
									p {
										color: $white;
									}
									.chat-time {
										color: $white;
									}
								}
								&:first-child {
									> div {
										border-top-right-radius: 0;
									}
								}
								.msg-action {
									margin-left: 0;
									margin-right: 1rem;
								}
							}
						}
					}
					&.media-attachment {
						margin-bottom: 1.125rem;
						.media-body > .msg-box {
							margin-bottom: 0.25rem;
							margin-right: 0.25rem;
						}
					}
				}
				.day-sep {
					position: relative;
					text-align: center;
					overflow: hidden;
					font-size: 0.875rem;
					margin: 1.875rem 0;
					text-transform: capitalize;
					&:before {
						background: $grey_light_4;
						content: "";
						height: 1px;
						right: 50%;
						position: absolute;
						top: 50%;
						width: 100%;
					}
					&:after {
						background: $grey_light_4;
						content: "";
						height: 1px;
						left: 50%;
						position: absolute;
						top: 50%;
						width: 100%;
					}
					span {
						display: inline-block;
						background: $white;
						z-index: 1;
						position: relative;
						padding: 0 0.625rem;
					}
				}
				.start-conversation {
					display: -webkit-box;
					display: -ms-flexbox;
					display: flex;
					-webkit-box-align: center;
					-ms-flex-align: center;
					align-items: center;
					justify-content: center;
					height: calc(100vh - 195px);
					flex-flow: column;
				}
			}
			footer.chat-footer {
				display: -webkit-box;
				display: -ms-flexbox;
				display: flex;
				-webkit-box-align: center;
				-ms-flex-align: center;
				align-items: center;
				padding: 0.75rem 1.25rem;
				background: $white;
				border-top: 1px solid $grey_light_4;
				margin-top: -1px;
				position: relative;
				@include transition(all 0.2s ease);
				.feather-icon,
				.svg-icon {
					display: block;
				}
				.input-group {
					width: 100%;
					.form-control {
						margin: 0 0.75rem;
					}
				}
			}
			.chat-info {
				position: absolute;
				top: 65px;
				right: -370px;
				width: 370px;
				margin-left: 0;
				bottom: 0;
				z-index: 100;
				border: none;
				background: $white;
				border-left: 1px solid $grey_light_4;
				@include transition(all 0.2s ease);
				.nicescroll-bar {
					padding: 1.25rem;
				}
				.cp-name {
					font-size: 1.25rem;
					font-weight: 500;
					color: $black_2;
				}
				.cp-info {
					padding-left: 0;
					li {
						a {
							color: $black_2;
							display: -webkit-box;
							display: -ms-flexbox;
							display: flex;
							-webkit-box-align: center;
							-ms-flex-align: center;
							align-items: center;
							.cp-icon-wrap {
								display: -webkit-box;
								display: -ms-flexbox;
								display: flex;
								margin-right: 0.5rem;
								.feather-icon,
								.svg-icon {
									font-size: 1.125rem;
								}
							}
						}
						margin-bottom: 0.625rem;
					}
				}
				.cp-action {
					padding-left: 0;
					li {
						a {
							color: $black_2;
						}
						margin-bottom: 0.625rem;
						&:last-child {
							margin-bottom: 0.25rem;
						}
					}
				}
				.cp-files {
					margin: 0 -0.8rem;
					padding-left: 0;
					li {
						padding: 0.8rem;
						border-radius: 0.5rem;
						@include transition(0.2s ease);
						margin-bottom: 0.25rem;
						&:last-child {
							margin-bottom: 0;
						}
						.media {
							-webkit-box-align: center;
							-ms-flex-align: center;
							align-items: center;
							.media-head {
								margin-right: 1rem;
								.avatar.avatar-sm {
									.avatar-icon {
										font-size: 1.75rem;
									}
								}
							}
							.media-body {
								display: -webkit-box;
								display: -ms-flexbox;
								display: flex;
								-webkit-box-align: center;
								-ms-flex-align: center;
								align-items: center;
								min-width: 0;
								> div {
									&:first-child {
										min-width: 0;
										-ms-flex: 1;
										flex: 1;
									}
								}
								.file-name {
									color: $black_2;
									overflow: hidden;
									text-overflow: ellipsis;
									white-space: nowrap;
									font-weight: 500;
								}
								.file-size {
									margin-top: 0.25rem;
									font-size: 0.875rem;
								}
							}
						}
						&:hover {
							background-color: #f2f2f2;
							.media {
								.media-body {
									.file-name {
										color: $primary;
									}
									a.cp-file-action {
										color: $black_2;
									}
								}
							}
						}
					}
				}
			}
		}
	}
	&.chatapp-info-active {
		.chatapp-single-chat {
			.chat-body {
				margin-right: 370px;
			}
			.chat-info {
				right: 0;
			}
			footer.chat-footer {
				margin-right: 370px;
			}
		}
	}

	.invite-user-list {
		padding: 0 1rem;
		> li {
			display: -webkit-box;
			display: -ms-flexbox;
			display: flex;
			-webkit-box-align: center;
			-ms-flex-align: center;
			align-items: center;
			justify-content: space-between;
			-webkit-justify-content: space-between;
			-ms-flex-pack: space-between;
			margin-bottom: 0.625rem;
			.user-name {
				color: $black_2;
				font-weight: 500;
			}
		}
	}
}
.invite-user-list {
	padding: 0 1rem;
	> li {
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-align: center;
		-ms-flex-align: center;
		align-items: center;
		justify-content: space-between;
		-webkit-justify-content: space-between;
		-ms-flex-pack: space-between;
		margin-bottom: 0.625rem;
		.user-name {
			color: $black_2;
			font-weight: 500;
		}
	}
}
.chatapp-call-window {
	.modal-content {
		min-height: calc(100vh - 100px);
		.modal-header {
			padding: 0.3rem 1.25rem;
			.modal-title {
				text-align: center;
				width: 100%;
				font-size: 0.875rem;
			}
			> .modal-action {
				display: -webkit-box;
				display: -ms-flexbox;
				display: flex;
				right: 1.25rem;
				position: absolute;
			}
		}
		.modal-body {
			display: flex;
			align-items: center;
			flex-flow: column;
			justify-content: center;
			padding: 3.125rem;
		}
		.modal-footer {
			border-top: 0;
			min-height: 160px;
			justify-content: center;
			.chatapp-caller-img {
				right: 30px;
				bottom: 30px;
				position: absolute;
			}
		}
		&.fullscreen {
			position: fixed;
			top: 0;
			bottom: 0;
			left: 0;
			overflow-y: auto;
			border-radius: 0;
			.modal-header {
				border-radius: 0;
			}
		}
	}
}
.hk-wrapper[data-navbar-style="collapsed"] {
	.chatapp-wrap {
		height: 100vh;
	}
}
.one {
	opacity: 0;
	-webkit-animation: dot 1.3s infinite;
	-webkit-animation-delay: 0s;
	animation: dot 1.3s infinite;
	animation-delay: 0s;
}
.two {
	opacity: 0;
	-webkit-animation: dot 1.3s infinite;
	-webkit-animation-delay: 0.2s;
	animation: dot 1.3s infinite;
	animation-delay: 0.2s;
}
.three {
	opacity: 0;
	-webkit-animation: dot 1.3s infinite;
	-webkit-animation-delay: 0.3s;
	animation: dot 1.3s infinite;
	animation-delay: 0.3s;
}
@-webkit-keyframes dot {
	0% {
		opacity: 0;
	}
	50% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
@keyframes dot {
	0% {
		opacity: 0;
	}
	50% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
@media (max-width: 1599px) {
	.chatapp-wrap {
		.chatapp-content {
			.chatapp-single-chat {
				.chat-info {
					right: -320px;
					width: 320px;
				}
				.chat-body {
					ul.list-unstyled {
						width: 100%;
					}
				}
			}
			.chatapp-aside {
				width: 320px;
			}
		}
		&.chatapp-info-active {
			.chatapp-single-chat {
				.chat-info {
					right: 0;
				}
				.chat-body {
