/*Checklist List*/
.hk-checklist {
	.form-check {
		margin-bottom: 0.625rem;
		position: relative;
		.form-check-label {
			position: relative;
		}
		.done-strikethrough {
			display: block;
			height: 12px;
			background: $red;
			mix-blend-mode: multiply;
			opacity: 0.08;
			width: 0;
			transition: width 0.35s ease-in-out, opacity 0.25s ease-in-out;
			position: absolute;
			margin-left: 0;
			top: 35%;
			left: 0;
			pointer-events: none;
			content: "";
		}
		&.form-check input:checked + label {
			.done-strikethrough {
				width: 100%;
			}
		}
		.delete-checklist {
			position: absolute;
			right: 0;
			opacity: 0;
			@include transition(opacity 0.2s ease-in-out);
		}
		&:hover {
			.delete-checklist {
				opacity: 1;
			}
		}
	}
	.add-new-checklist {
		.feather-icon {
			margin-right: 0.25rem;
		}
	}
	.form-control {
		&.checklist-input {
			position: absolute;
			top: 0;
			border: none;
			box-shadow: none;
			padding: 0;
			height: auto;
			width: auto;
		}
	}
}

